<script>

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'jumbotron',
  data() {
    return {
      randomProfile: "",
      titleY: 0,
      titleAlpha: 100
    }
  },
  created() {
    const randomNumber = Math.floor(Math.random() * 3);
    this.randomProfile = require("../assets/jumbotron/" + (randomNumber + 1+2) + ".png");
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll);
  },
  unmounted () {
    window.removeEventListener('scroll', this.handleScroll);
  },
  methods: {
    // eslint-disable-next-line no-unused-vars
    handleScroll (event) {
      //console.log(window.scrollY)
      this.titleY = window.scrollY / 2;
      this.titleAlpha = 100 - window.scrollY / 16;
    }
  }
}
</script>

<template>
  <div class="jumbotron" :style="{
    backgroundImage : `linear-gradient(rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.2)), url(${this.randomProfile})`
  }">
    <div class="text-container" :style="{
      top: `calc(50% + ${titleY}px)`,
      opacity: `${titleAlpha}%`
    }">
      <span class="jumbotron-title">안녕하세요!</span>
      <br>
      <span class="jumbotron-subtitle">HI!</span>
    </div>
    <svg class="waves" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="0 24 150 28" preserveAspectRatio="none" shape-rendering="auto">
      <defs>
        <path id="gentle-wave" d="M-160 44c30 0 58-18 88-18s 58 18 88 18 58-18 88-18 58 18 88 18 v44h-352z" />
      </defs>
      <g class="parallax">
        <use xlink:href="#gentle-wave" x="48" y="0" fill="rgba(32, 32, 32, 0.7" />
        <use xlink:href="#gentle-wave" x="48" y="3" fill="rgba(48, 48, 48, 0.5)" />
        <use xlink:href="#gentle-wave" x="48" y="5" fill="rgba(64, 64, 64, 0.3)" />
        <use xlink:href="#gentle-wave" x="48" y="7" fill="rgb(24, 24, 24)" />
      </g>
    </svg>
  </div>
</template>

<style scoped>
.jumbotron {
  background-size: cover;
  background-position: center;
  height: 100vh;
  place-items: center;
  user-select:none;

  position: relative;
  z-index: -50;
}

.text-container {
  position: absolute;

  left: 50%;
  transform: translate(-50%, -50%);

  width: 100%;

  z-index: -1;
}

.jumbotron-title {
  font-size: calc(105px + 2vmin);
  color: white;

  font-family: 'Black Han Sans', sans-serif;
}

.jumbotron-subtitle {
  font-size: calc(40px + 2vmin);
  color: white;

  font-family: 'Black Han Sans', sans-serif;
}

.waves {
  bottom: 0;

  position: absolute;
  z-index: -1;

  left: 50%;
  transform: translate(-50%, 0);
  width: 100%;
  height: 15vh;
  margin-bottom: -7px; /*Fix for safari gap*/
  min-height: 50px;
  max-height: 150px;
}

.parallax > use {
  animation: move-forever 25s cubic-bezier(.55,.5,.45,.5)     infinite;
}
.parallax > use:nth-child(1) {
  animation-delay: -2s;
  animation-duration: 7s;
}
.parallax > use:nth-child(2) {
  animation-delay: -3s;
  animation-duration: 10s;
}
.parallax > use:nth-child(3) {
  animation-delay: -4s;
  animation-duration: 13s;
}
.parallax > use:nth-child(4) {
  animation-delay: -5s;
  animation-duration: 20s;
}

@keyframes move-forever {
  0% {
    transform: translate3d(-90px,0,0);
  }
  100% {
    transform: translate3d(85px,0,0);
  }
}
/*Shrinking for mobile*/
@media (max-width: 768px) {
  .waves {
    height:40px;
    min-height:40px;
  }
}
</style>